/* eslint-disable consistent-return */
const defineStartX = require('./defineStartX');
const defineStartY = require('./defineStartY');

export default function fillUnit(x, y, canvas, matrix) { // color part matrix
  const startX = defineStartX(x, matrix);
  const startY = defineStartY(y, matrix);
  canvas.beginPath();
  canvas.fillRect(startX, startY, 526 / matrix, 526 / matrix);
  canvas.fill();
}
